import React, { useState, useEffect } from 'react';
import articleContent from './article-content';
import ArticlesList from '../components/articlesList';
import UpvoteSection from '../components/UpvoteSection';
import CommentsList from '../components/CommentsList';
import NotFoundPage from './NotFoundPage';

const ArticlesPage = ({ match }) => {
    const name = match.params.name;
    const article = articleContent.find(article => article.name === name);

    const [articleInfo, setArticleInfo] = useState({
        upvotes: 0,
        comments: []
    });
    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();

            setArticleInfo(body);
        }
        fetchData();
    }, [name]);


    if (!article) {
        return <NotFoundPage />
    }
    const others = articleContent.filter((article) => (
        article.name !== name
    ));
    return (
        <>
            <h1>{article.title} </h1>
            <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            {article.content.map((paragraph, key) => ( // putting each paragraph from the articles content into a signle paragraph on the page
                <p key={key}>{paragraph}</p>
            ))}
            <CommentsList comments={articleInfo.comments} />
            <h3>Other Articles:</h3>
            <ArticlesList articles={others} />
        </>
    );
}

export default ArticlesPage;    